<template>
  <div class="order">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="order-box">
      <div class="address" @click="openPopup">{{userAddress.address}}</div>
      <div class="order-content">
        <div class="order-item">
          <div class="items" v-for="(item,index) in orderData" :key="index">
            <div class="clearfix">
              <div class="pro-img fl">
                <img class="auto-img" :src="item.small_img" alt />
              </div>
              <div class="fl pro-info">
                <div class="fl">
                  <div class="names">
                    <div class="zh-name">{{item.name}}</div>
                    <div class="en-name">{{item.enname}}</div>
                  </div>
                  <div class="rule">{{item.rule}}</div>
                </div>
                <div class="fr">
                  <div class="price">￥{{item.price}}</div>
                  <div class="price">x{{item.count}}</div>
                </div>
              </div>
            </div>
          </div>
          <div class="order-total clearfix">
            <div class="fr clearfix">
              <span class="fl total-count">共计{{counts}}件商品 合计：</span>
              <span class="fl total-price">￥{{total}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-submit-bar :price="total * 100" button-text="立即结算" button-type="info" @submit="commit" />
    <van-popup class="popBox" v-model="isOpen" position="bottom">
      <van-address-list
        v-model="aid"
        :list="addressList"
        default-tag-text="默认"
        @select="selectAddress"
        @add="addAddress"
        @edit="editAddress"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: false,
      aid: "",
      addressList: [],
      userAddress: {
        address: "请选择收货地址",
        phone: "",
        receiver: ""
      },
      orderData: [],
      //商品总数量
      counts: 0,
      //总金额
      total: 0,
      sids: []
    };
  },
  created() {
    //截取查询参数sids
    this.sids = this.$route.query.sids.split("-");
    this.getAddressData();
    this.getPayOrderData(this.sids);
  },
  methods: {
    addAddress() {
      this.$router.push({ name: "Address" });
    },
    editAddress() {
      this.$router.push({ name: "MyAddress" });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    openPopup() {
      this.isOpen = true;
    },
    //获取地址数据
    getAddressData() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 20000) {
            result.data.result.forEach(v => {
              let currentAddress = {
                id: v.id,
                tel: v.tel,
                name: v.name,
                address:
                  v.province +
                  " " +
                  v.city +
                  " " +
                  v.county +
                  " " +
                  v.addressDetail,
                isDefault: Boolean(v.isDefault)
              };
              //默认选择默认地址
              if (v.isDefault) {
                this.aid = v.aid;
                //如果存在默认地址，则默认选择默认地址
                this.userAddress.address = currentAddress.address;
              }
              this.addressList.push(currentAddress);
            });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //选择地址
    selectAddress(item) {
      this.isOpen = false;
      this.address = item.address;
      this.userAddress = {
        address: item.address,
        phone: item.tel,
        receiver: item.names
      };
    },
    //获取提交订单数据
    getPayOrderData(sids) {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 50000) {
            result.data.result.forEach(v => {
              this.counts += v.count;
              this.total += v.count * v.price;
            });
            this.orderData = result.data.result;
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //提交订单
    commit() {
      if (this.userAddress.address == "请选择收货地址") {
        this.$toast("请选择收货地址");
        return;
      }
      //获取token
      let tokenString = localStorage.getItem("__TK");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/pay",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
          phone: this.userAddress.phone,
          address: this.userAddress.address,
          receiver: this.userAddress.receiver
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 60000) {
            this.$router.push({ name: "Order" });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    }
  }
};
</script>

<style lang="less" scoped>
.order {
  .order-item {
    padding: 10px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 0 2px 2px rgba(215, 180, 148, 0.4);
  }
  .pro-img {
    width: 60px;
    height: 60px;
    background-color: #ddd;
  }
  .pro-info {
    width: calc(~"100% - 70px");
    margin-left: 10px;
  }
  .names {
    height: 40px;
  }
  .zh-name {
    font-size: 14px;
  }
  .en-name {
    font-size: 12px;
    color: #999;
    margin-top: 4px;
  }
  .rule {
    height: 20px;
    font-size: 12px;
    color: #999;
    line-height: 20px;
  }
  .price {
    height: 40px;
    color: rgb(215, 180, 148);
    font-size: 16px;
    text-align: right;
  }
  div.price:nth-child(2) {
    color: brown;
    font-size: 20px;
  }
  .count {
    height: 20px;
    color: #999;
    font-size: 12px;
    line-height: 20px;
    text-align: right;
  }
  .items {
    margin-bottom: 10px;
  }
  .order-total {
    margin-top: 20px;
  }
  .total-count {
    font-size: 12px;
    color: #999;
    height: 40px;
    line-height: 40px;
  }
  .total-price {
    font-weight: bold;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
    color: rgb(215, 180, 148);
  }
  /deep/ .van-submit-bar__price {
    color: brown;
  }
  .popBox {
    min-height: 300px;
    max-width: 580px;
    overflow-y: auto;
  }
  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    background-color: brown;
    border-color: brown;
    color: #fff;
  }
  /deep/ .van-tag--danger {
    background-color: brown;
  }
  /deep/ .van-button--danger {
    background-image: linear-gradient(rgb(215, 180, 148), brown);
    border: none;
  }
  /deep/ .van-button--info {
    width: 50%;
    background-image: linear-gradient(rgb(215, 180, 148), brown);
  }
  .address {
    height: 40px;
    line-height: 40px;
    padding: 0 10px;
    background-color: #fff;
    border-radius: 10px;
    font-size: 14px;
  }
  .order-box {
    padding: 10px;
  }
  .order-content {
    margin-top: 20px;
  }
}
</style>